<template>
  <v-dialog v-model="isActive" max-width="500">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn v-bind="activatorProps" title="編輯" icon="mdi-pencil" size="small" variant="text"></v-btn>
    </template>

    <template v-slot:default="{ isActive }">
      <v-card class="review-border">
        <v-card-title class="d-flex justify-space-between align-center">
          <span class="text-blue">編輯單位</span>
          <v-btn icon variant="text" @click="cancele()">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>
        <v-card-text>
          <v-text-field hide-details="auto" v-model="form.unitCode" density="compact" label="角色編號"
            prepend-inner-icon="mdi-music-accidental-sharp" variant="solo" required class="mb-5">
            <template #label>
              單位編號
            </template>
          </v-text-field>
          <v-text-field hide-details="auto" v-model="form.unitName" density="compact" label="角色名稱"
            prepend-inner-icon="mdi-account" variant="solo" required class="mb-5">
            <template #label>
              單位名稱
            </template>
          </v-text-field>
          <v-textarea v-model="form.description" label="描述" density="compact" variant="solo" required
            placeholder="描述"></v-textarea>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn text="取消" variant="outlined" color="grey-darken-2" @click="cancele()"></v-btn>
          <v-btn text="確認" variant="outlined" color="primary" @click="submit()"></v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script setup>
import { onMounted } from 'vue';
import { unit } from '@/requests';
const props = defineProps({
  unit: {
    type: Object,
    required: true,
    default: () => ({
      id: "",
      unitCode: "",
      unitName: "",
      description: "",
    }),
  },
});

const form = ref({
  id: "",
  unitCode: "",
  unitName: "",
  description: "",
})
onMounted(() => {
  form.value.id = props.unit.id;
  form.value.unitCode = props.unit.unitCode;
  form.value.unitName = props.unit.unitName;
  form.value.description = props.unit.description;
});
const isActive = ref(false);
const emit = defineEmits(['edit-unit']);
function submit() {
  unit.update({ id: form.value.id }, form.value).then(() => {
    emit('edit-unit', { ...form.value });
    isActive.value = false
  }).catch((error) => {
    alert("編輯失敗，請重新整理後再試一次");
    isActive.value = false;
  });
}
function cancele() {
  form.value.id = props.unit.id;
  form.value.unitCode = props.unit.unitCode;
  form.value.unitName = props.unit.unitName;
  form.value.description = props.unit.description;
  isActive.value = false
}
</script>
<style scoped>
.review-border {
  padding: 15px;
}

.review-button-edit {
  border-radius: 100px !important;
  /* height: 28px !important; */
  width: 60px !important;
}

.review-button-circle {
  font-size: 24px;
}

.review-button-content {
  font-size: 12px;
  position: relative;
  top: -5px;
}
</style>